*{margin:0;padding:0}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.container{max-width:890px;margin:0 auto;padding:0 15px}.form-loading{width:100%}@media (max-width: 720px){.form-loading{max-width:100%}}.form-loading__inner{display:flex;flex-direction:column;margin-top:15px}.form-loading__input{font-size:14px;color:#333333;padding:10px;border:1px solid #bebebe;outline:none;margin-top:10px;border-radius:4px;background-color:#f7f7f7;position:relative}.form-loading__input:focus{box-shadow:0px 0px 5px 2px #2dc0c5}.form-loading__btn{font-size:18px;font-weight:700;color:#fff;background-color:#2dc0c5;border:none;padding:13px 0;width:100%;margin-top:20px;outline:none;cursor:pointer}.form-loading__btn:hover{background-color:#28a2a7}.form-loading__btn:active{background-color:#25868a}.gallary{display:flex;flex-wrap:wrap;justify-content:center;margin-top:15px}.gallary__empty{text-align:center;font-size:18px;margin-top:15px}.gallary__item{position:relative;width:calc(25% - 8px);height:200px;margin:0 10px 10px 0;cursor:pointer;border-radius:4px;transition:transform 0.2s ease-in-out}@media (max-width: 720px){.gallary__item:nth-child(2n){margin-right:0}}.gallary__item:nth-child(4n){margin-right:0}.gallary__item:hover{transform:scale(1.05)}.gallary__item:hover .gallary__descr{opacity:1}@media (max-width: 720px){.gallary__item{width:calc(50% - 5px)}}@media (max-width: 500px){.gallary__item{width:100%}}.gallary__image{width:100%;height:100%;object-fit:cover;border-radius:4px}.gallary__image--width{max-width:calc(100% - 30px);max-height:80%;display:block;object-fit:cover;margin:0 auto;margin-top:20px;border:5px solid #fff;border-radius:16px}.gallary__descr{background-color:rgba(100,100,100,0.6);display:flex;opacity:0;position:absolute;top:0;right:0;bottom:0;left:0;color:#fff;justify-content:center;align-items:center;border-radius:4px;transition:opacity 0.2s ease-in-out}.gallary__full{position:fixed;background-color:rgba(0,0,0,0.9);top:0;left:0;right:0;bottom:0}.gallary__inner{display:flex;justify-content:flex-end;padding:15px 15px 0 15px}.gallary__close,.gallary__delete{width:35px;cursor:pointer;padding:10px;transition:transform 0.2s ease-in-out}.gallary__close:hover,.gallary__delete:hover{transform:scale(1.2)}

